import { getDefaultOptions, request } from './helpers';

const get = url => {
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};

const post = (url, body) => {
  const options = {
    ...getDefaultOptions(),
    method: 'POST',
    body: JSON.stringify(body),
  };
  return request(url, options);
};

const put = (url, body) => {
  const options = {
    ...getDefaultOptions(),
    method: 'PUT',
    body: JSON.stringify(body),
  };
  return request(url, options);
};

export const apiGetRichiesta = async (serviceUrl, id) => {
  return get(`${serviceUrl}/api/richiesta/esito?id=${id}`);
};

export const apiAccettaProposta = async (serviceUrl, id) => {
  return put(`${serviceUrl}/api/richiesta/accetta?id=${id}`);
};

export const apiRifiutaProposta = async (serviceUrl, id) => {
  return put(`${serviceUrl}/api/richiesta/rifiuta?id=${id}`);
};
