import React, { useEffect, useState } from 'react';
import { Button, Col, Row, Spinner, Toast, ToastBody, ToastContainer } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import Modal from 'react-bootstrap/Modal';

import {
  faCheckCircle,
  faCircleXmark,
  faPlaneCircleXmark,
  faThumbsDown,
  faXmarkCircle,
  faXmarksLines,
  faXmarkSquare,
} from '@fortawesome/free-solid-svg-icons';
import { K } from '../../Constants';
import { apiAccettaProposta, apiGetRichiesta, apiRifiutaProposta } from '../../http/api';
import { useQuery } from '../../http/helpers';

export const Esito = () => {
  const [richiesta, setRichiesta] = useState();
  const [proposta, setProposta] = useState(false);
  const [card, setCard] = useState(false);
  const [errorCode, setErrorCode] = useState('');
  const [showModal, setShowModal] = useState(false);



  console.log(richiesta);

  const getRichiesta = query => {
    apiGetRichiesta(K.serviceUrl, query)
      .then(json => {
        setRichiesta(json);
      })
      .catch(e => {
        console.log(e);
        setErrorCode(e);
        setCard(true);
      });
  };

  const accetta = () => {
    apiAccettaProposta(K.serviceUrl, query)
      .then(json => {
        console.log('ok');
        window.location.href = `/entando-de-app/en/dettaglio.page?guid=${json.guid}`;
      })
      .catch(e => console.log('err put'));
  };

  const rifiuta = id => {
    apiRifiutaProposta(K.serviceUrl, id)
      .then(json => {
        console.log('ok');
      })
      .catch(e => console.log('err put'));
  };

  const query = useQuery().get('id');

  useEffect(() => {
    getRichiesta(query);
  }, []);

  function currencyFormatDE(num) {
    return `${num
      .toFixed(2) // always two decimal digits
      .replace('.', ',') // replace decimal point character with ,
      .replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1.')} €`; // use . as a separator
  }

  return (
    <>
      {card === true && (
        <div className="card text-center">
          <div className="card-body">
            <h5 className="card-title">Errore di sistema</h5>

            {errorCode === 409 && <p className="card-text">{K.errEsitoNegativoEsistente}</p>}
            {errorCode === 400 && <p className="card-text">{K.errPivaNotValid}</p>}
            {errorCode === 408 && <p className="card-text">{K.errTigranTimeout}</p>}
            {errorCode === 422 && <p className="card-text">{K.errPraticaEsistente}</p>}
            {errorCode === 500 && <p className="card-text">{K.errGenerico}</p>}

            <button
              className="btn btn-primary"
              onClick={event => (window.location.href = '/entando-de-app')}
            >
              Torna alla Home
            </button>
          </div>
        </div>
      )}

      {card === false && (
        <>
          {!richiesta ? (
            <>
              <div className="spinner">
                <div className="d-flex flex-column align-items-center justify-content-center">
                  <Row>
                    <Spinner animation="grow" variant="primary" />
                  </Row>
                  <Row className="row">
                    <strong className="mt-2">Elaborazione richiesta ...</strong>
                  </Row>
                </div>
              </div>
            </>
          ) : (
            <>
              {proposta === false ? (
                <div className="align-items-center back-col p-4">
                  <div className="white-div align-content-center">
                    {richiesta.esito === true ? (
                      <>
                        <Row>
                          <Col md={12} className="align-to-center">
                            <FontAwesomeIcon
                              icon={faCheckCircle}
                              size="6x"
                              className="green-check mb-4"
                            />
                          </Col>
                        </Row>
                        <Row>
                          <Col md={12} className="align-to-center">
                            <p className="title-esito align-content-center">
                              La tua azienda è risultata finanziabile.
                            </p>
                          </Col>
                        </Row>

                        <Row>
                          <Col md={12} className="align-to-center">
                            <p className="subtitle">
                              Per completare la pratica leggi ed accetta la nostra proposta
                              commerciale{' '}
                            </p>
                          </Col>
                        </Row>

                        <Row>
                          <Col md={12} className="align-to-center">
                            <button
                              className="custom-button-richiesta"
                              onClick={() => setProposta(true)}
                            >
                              {' '}
                              Vai alla proposta
                            </button>
                          </Col>
                        </Row>
                        <Row>
                          <Col md={12} className="align-to-center">
                            <button
                              className="custom-button-richiesta"
                              onClick={event => (window.location.href = '/entando-de-app')}
                            >
                              {' '}
                              Ritorna alla home page
                            </button>
                          </Col>
                        </Row>
                      </>
                    ) : (
                      <>
                        <Row>
                          <Col md={12} className="align-to-center">
                            <FontAwesomeIcon
                              icon={faCircleXmark}
                              size="6x"
                              color="red"
                              className="mb-4"
                            />
                          </Col>
                        </Row>
                        <Row>
                          <Col md={12} className="align-to-center">
                            <p className="title-esito align-to-center">
                              Siamo spiacenti, al momento non possiamo soddisfare la sua richiesta
                              di finanziamento
                            </p>
                          </Col>
                        </Row>

                        <Row>
                          <Col md={12} className="align-to-center">
                            <button
                              className="custom-button-richiesta"
                              onClick={event => (window.location.href = '/entando-de-app')}
                            >
                              {' '}
                              Ritorna alla home page
                            </button>
                          </Col>
                        </Row>
                      </>
                    )}
                  </div>
                </div>
              ) : (
                <div className="align-items-center back-col p-4">
                  <div className="white-div align-content-center">
                    <Modal show={showModal} onHide={() => setShowModal(false)}>
                      <Modal.Header closeButton>
                        <Modal.Title>Non Accettare</Modal.Title>
                      </Modal.Header>

                      <Modal.Body>
                        <p>Sei sicuro di non volere accettare la nostra proposta?</p>
                      </Modal.Body>

                      <Modal.Footer>
                        <Button variant="secondary" onClick={() => setShowModal(false)}>
                          No
                        </Button>
                        <Button
                          variant="primary"
                          onClick={() => {
                            rifiuta(richiesta.id);
                            window.location.href = '/entando-de-app';
                          }}
                        >
                          Si
                        </Button>
                      </Modal.Footer>
                    </Modal>

                    <Row>
                      <Col md={12} className="align-to-center">
                        <p className="title-esito align-content-center">
                          La Prefattibilità della tua richiesta ha dato esito positivo
                        </p>
                      </Col>
                    </Row>

                    <Row>
                      <Col md={12} className="align-to-center">
                        <p className="subtitle">
                          Possiamo avviare il nostro processo di delibera per la seguente operazione{' '}
                        </p>
                      </Col>
                    </Row>

                    <Row>
                      <Col md={12} className="align-to-center">
                        <span className="subtitle-proposta">Importo: </span>{' '}
                        <span className="subtitle">
                          {richiesta.importo ? currencyFormatDE(richiesta.importo) : ''}
                        </span>
                      </Col>
                    </Row>
                    <Row>
                      <Col md={12} className="align-to-center">
                        <span className="subtitle-proposta">Durata: </span>{' '}
                        <span className="subtitle">{richiesta.durata}</span>
                        <span className="subtitle">
                          {richiesta.preammortamento === '12'
                            ? ' di cui 12 di preammortamento'
                            : ''}
                        </span>
                      </Col>
                    </Row>
                    <Row>
                      <Col md={12} className="align-to-center">
                        <span className="subtitle-proposta">Tasso variabile: </span>{' '}
                        <span className="subtitle">
                          {' '}
                          euribor mese + spread {richiesta.tassoVariabile}%
                        </span>
                      </Col>
                    </Row>
                    <Row>
                      <Col md={12} className="align-to-center">
                        <span className="subtitle-proposta">Spese Istruttoria: </span>{' '}
                        <span className="subtitle">2% del finanziamento</span>
                      </Col>
                    </Row>

                    <Row>
                      <Col md={12} className="align-to-center">
                        <button
                          className="custom-button-richiesta mx-2"
                          onClick={() => setShowModal(true)}
                        >
                          Non accettare
                        </button>
                        <button className="custom-button-richiesta mx-2" onClick={() => accetta()}>
                          Accetta
                        </button>
                      </Col>
                    </Row>
                  </div>
                </div>
              )}
            </>
          )}
        </>
      )}
    </>
  );
};
